.navbar-layout{

    .burger-menu{
        label{
            display:none;
        }
      
        input{
            display:none;
        }
      }
    
    .rgb-border{
        &::before {
            content: '';
            position: absolute;
            display: block;
            top:100%;
            left:50%;
            width: 50%;
            height: 3px;
            background:#62DB54;
        }
    
        &::after {
            content: '';
            position: absolute;
            display: block;
            top:100%;
            left:0%;
            width: 50%;
            height: 3px;
            background:#09ACF5;
        }
    }

    @media(max-width:767px){
        .burger-menu{
            label{
                display:block;
            }

            input{
                display:none;
            }
        }

        .menu-content-layout{
            position: absolute;
            top:100%;
            left:0px;
            z-index: -100;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            /* height: 100%; ????????*/
            background-color: #2D2D2D;
            transform:translateY(-200%);
            transition: all 0.5s;
        }

        #toggle:checked ~ .menu-content-layout {
            transform: translateY(0%);
          }
    
        #toggle:checked ~ .menu-content-layout {
            border:1px solid white;
        }
    }
}